import styled from 'styled-components';
import { darken } from 'polished';
import { themeLightBase } from '../../../../styles/themes/variables';


const generatePseudoStyles = (width: number) => {
  return `
  &.percent-${width} {
    &::after {
      content: ' ';
      width: ${width}%;
      position: absolute;
      left: 0;
      top: 0;
      height: 5px;
      background-color: ${themeLightBase};
    }
  }
  `;
}

export const StyledRatingContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  height: 100%;

  .rating__item {
    width: 20%;
    display: inline-block;
    height: 5px;
    position: relative;
    background-color: ${darken(.3, themeLightBase)};
    margin: 0 5px;

    ${generatePseudoStyles(25)}
    ${generatePseudoStyles(50)}
    ${generatePseudoStyles(75)}
    ${generatePseudoStyles(100)}

    &::after {
      transition: all 0.5s linear;
    }
  }
`;
