import { Row } from "react-bootstrap";
import { PhotographDetails } from "../../../models/user-details.model";
import { StyledCol } from "./Photography.styles";

interface PhotographyProps {
  photosTaken: PhotographDetails
}

const Photography: React.FC<PhotographyProps> = ({ photosTaken }) => {

  return (
    <Row className="my-4">
      {photosTaken.details.map(pic => (
        <StyledCol key={pic.title.split(' ').join('_')} xs="6" lg="4" className="mb-4 align-items-center d-flex">
          <img src={process.env.PUBLIC_URL + pic.image} alt={`${pic.title}`} />
        </StyledCol>
      ))}
    </Row>
  )
};

export default Photography;
