export const media_xs = (content: string) => {
  return `@media (max-width: 576px) { ${content} }`;
}

export const media_sm_up = (content: string) => {
  return `@media (min-width: 768px) { ${content} }`;
}

export const media_md_up = (content: string) => {
  return `@media (min-width: 992px) { ${content} }`;
}

export const media_lg_up = (content: string) => {
  return `@media (min-width: 1200px) { ${content} }`;
}

export const media_xl_up = (content: string) => {
  return `@media (min-width: 1400px) { ${content} }`;
}
