import axios from 'axios';
import { Dispatch } from 'redux';
import { TechDetails } from '../../models/tech-details.model';
import { TechDetailActionTypes } from '../action-types';
import { TechDetailsAction } from '../actions';

export const fetchTechDetails = () => {
  return async (dispatch: Dispatch<TechDetailsAction>) => {
    dispatch({
      type: TechDetailActionTypes.FETCH_TECH_DETAILS
    });

    try {
      const { data } = await axios.get<TechDetails[]>(process.env.PUBLIC_URL + '/mock-data/tech-proficiency.json');
      dispatch({
        type: TechDetailActionTypes.FETCH_TECH_DETAILS_SUCCESS,
        payload: data
      });
    } catch (err: any) {
      dispatch({
        type: TechDetailActionTypes.FETCH_TECH_DETAILS_FAILURE,
        payload: err.message
      })
    }
  }
}
