
import React, { useState } from 'react';
import ExperienceTile from './ExperienceTile/ExperienceTile';

import { useTypedSelector } from '../../hooks/useTypedSelector';
import { WorkExperience } from '../../models/work-experience.model';
import { WorkExperienceViewProps } from './index.types';
import ExperienceDetailsModal from './ExperienceDetailsModal/ExperienceDetailsModal';

import './index.scss';
import { Container, Row, Col } from 'react-bootstrap';

const WorkExperienceView:React.FC<WorkExperienceViewProps> = ({className}) => {

  const [selectedExperience, setSelectedExperience] = useState<WorkExperience>();

  const workEx = useTypedSelector(state => state.workExperience);

  return (
    <div className={`xp-timeline ${className ? className : ''}`} >
      <Container>
        <Row>
          <Col>
            {workEx.loading && <span>Loading Work Experience</span>}
            {workEx.error && <span>{workEx.error}</span>}
            {workEx.data && (
              <section className="timeline">
                <ul className="timeline__list">
                  {workEx.data.map((exp:WorkExperience, index:number) => (
                    <li key={`${exp.title}-${index}-li`} className="timeline__list__item">
                      <ExperienceTile key={`${exp.title}-${index}`} experience={exp} setSelectionCallback={setSelectedExperience} />
                    </li>
                  ))}
                </ul>
              </section>
            )}
            {selectedExperience && <ExperienceDetailsModal experience={selectedExperience} onCloseCallback={setSelectedExperience} />}
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default WorkExperienceView;
