import { Row } from "react-bootstrap";
import { DrawingDetails } from "../../../models/user-details.model";
import { StyledCol } from "./Drawing.styles";

interface DrawingProps {
  drawings: DrawingDetails
}

const Drawing: React.FC<DrawingProps> = ({ drawings }) => {

  return (
    <Row className="my-4">
      {drawings.details.map(pic => (
        <StyledCol key={pic.title.split(' ').join('_')} xs="6" md="4" lg="4" className="mb-4 align-items-center d-flex">
          <img src={process.env.PUBLIC_URL + pic.image} alt={`${pic.title}`} />
        </StyledCol>
      ))}
    </Row>
  )
};

export default Drawing;
