import { UserDetails } from '../../models/user-details.model';
import { UserActionTypes } from '../action-types';
import { UserAction } from '../actions';

interface UserState {
  loading: boolean;
  error: string | null;
  userDetails: UserDetails | null;
}

const initialState = {
  loading: false,
  error: null,
  userDetails: null,
};

const userReducer = (
  state: UserState = initialState,
  action: UserAction
): UserState => {
  switch (action.type) {
    case UserActionTypes.FETCH_USER_DETAILS:
      return { loading: true, error: null, userDetails: null };
    case UserActionTypes.FETCH_USER_DETAILS_SUCCESS:
      return { loading: false, error: null, userDetails: action.payload };
    case UserActionTypes.FETCH_USER_DETAILS_FAILURE:
      return { loading: false, error: action.payload, userDetails: null };
    default:
      return state;
  }
};

export default userReducer;
