import { useEffect, useState } from 'react';
import { Container, Nav, Navbar } from "react-bootstrap";
import { Outlet, Link, useLocation } from 'react-router-dom';
import { useActions } from "../../hooks/useActions";

import './Header.scss';

const Header: React.FC = () => {
  const { fetchUserDetails, fetchTechDetails, fetchWorkExperience } = useActions();
  const [expanded, setExpanded] = useState(false);

  useEffect(() => {
    fetchUserDetails();
    fetchTechDetails();
    fetchWorkExperience();
  });

  const location = useLocation();

  const isActive = (route: string | string[]) => {
    if (Array.isArray(route)) {
      console.log(location.pathname);
      if (route.indexOf(location.pathname) !== -1) {
        return true;
      }
    } else if (location.pathname.indexOf(route) !== -1) {
      return true;
    }
    return false;
  }

  const toggleNav = () => {
    setExpanded(!expanded);
  }
  
  return (
    <>
      <Navbar bg="dark" expand="sm" variant="dark" className="sp-header" fixed="top" expanded={expanded}>
        <Container>
          <Navbar.Brand as={Link} to='about'>SS</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" onClick={toggleNav} />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mx-auto d-flex justify-content-evenly">
              <Nav.Link as={Link} to='/about' onClick={toggleNav} className={`sp-header__link ${isActive(['/about', '/']) ? 'active': ''}`}>About</Nav.Link>
              <Nav.Link as={Link} to='/experience' onClick={toggleNav} className={`sp-header__link ${isActive('/experience') ? 'active': ''}`}>Experience</Nav.Link>
              <Nav.Link as={Link} to='/tech/list' onClick={toggleNav} className={`sp-header__link ${isActive('/tech') ? 'active': ''}`}>Technologies</Nav.Link>
              <Nav.Link as={Link} to='/interests' onClick={toggleNav} className={`sp-header__link ${isActive('/interests') ? 'active': ''}`}>Interests</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Outlet />
    </>
  );
}

export default Header;
