import { DateTime, Duration } from "luxon";

interface UserAboutInput {
  quote?: string;
  workingSince: string;
  workingGap?: number|null;
  aboutMe?: string;
}

export interface ReadingDetails {
  type: 'reading';
  details: {
    title: string;
    author: string;
    image: string;
  }[]
}

export interface PhotographDetails {
  type: 'photography';
  details: {
    title: string;
    image: string;
  }[]
}
export interface DrawingDetails {
  type: 'drawing';
  details: {
    title: string;
    image: string;
  }[]
}

export type InterestDetails = ReadingDetails | PhotographDetails | DrawingDetails;

export interface UserDetailsInput {
  firstName: string;
  lastName: string;
  previewEmail: string;
  about: UserAboutInput;
  interests: string[];
  interestDetails: InterestDetails[]
}

class UserAbout {
  constructor (details: UserAboutInput) {
    this.quote = details.quote;
    this.workingSince = DateTime.fromJSDate(new Date(details.workingSince));
    this.workingGap = details.workingGap ? Duration.fromObject({months: details.workingGap}) : null;
    this.aboutMe = details.aboutMe;
  }
  quote?: string;
  workingSince: DateTime;
  workingGap?: Duration|null;
  aboutMe?: string;
}

export class UserDetails {
  constructor(details: UserDetailsInput) {
    this.firstName = details.firstName;
    this.lastName = details.lastName;
    this.previewEmail = details.previewEmail;
    this.about = new UserAbout(details.about);
    this.interests = details.interests;
    this.interestDetails = details.interestDetails;
  }

  firstName: string;
  lastName: string;
  previewEmail: string;
  about: UserAbout;
  interests: string[];
  interestDetails: InterestDetails[]
}
