import axios from 'axios';
import { Dispatch } from 'redux';
import { WorkExperience, WorkExpSchema } from '../../models/work-experience.model';
import { WorkExperienceActionType } from '../action-types';
import { WorkExpAction } from '../actions';

export const fetchWorkExperience = () => {
  return async (dispatch: Dispatch<WorkExpAction>) => {
    dispatch({
      type: WorkExperienceActionType.FETCH_WORK_EXP,
    });

    try {
      const { data } = await axios.get<WorkExpSchema[]>(process.env.PUBLIC_URL + '/mock-data/work-experience.json');
      const workExDetails = data.map(item => new WorkExperience(item))

      dispatch({
        type: WorkExperienceActionType.FETCH_WORK_EXP_SUCCESS,
        payload: workExDetails,
      });
    } catch (err:any) {
      dispatch({
        type: WorkExperienceActionType.FETCH_WORK_EXP_FAILURE,
        payload: err.message,
      });
    }
  };
};
