import { TechDetails } from '../../models/tech-details.model';
import { TechDetailActionTypes } from '../action-types';
import { TechDetailsAction } from '../actions';

interface TechState {
  loading: boolean;
  error: string | null;
  techDetails: TechDetails[] | null;
};

const initialState = {
  loading: false,
  error: null,
  techDetails: null
};

const techDetailsReducer = (
  state: TechState = initialState,
  action: TechDetailsAction
): TechState => {
  switch (action.type) {
    case TechDetailActionTypes.FETCH_TECH_DETAILS:
      return { loading: true, error: null, techDetails: null };
    case TechDetailActionTypes.FETCH_TECH_DETAILS_SUCCESS:
      return { loading: false, error: null, techDetails: action.payload };
    case TechDetailActionTypes.FETCH_TECH_DETAILS_FAILURE:
      return { loading: false, error: action.payload, techDetails: null };
    default:
      return state;
  }
};

export default techDetailsReducer;
