import { Row } from "react-bootstrap";
import { ReadingDetails } from "../../../models/user-details.model";
import { StyledCol } from "./Reading.styles";

interface ReadingProps {
  booksRead: ReadingDetails
}

const Reading: React.FC<ReadingProps> = ({ booksRead }) => {

  return (
    <Row className="my-4">
      {booksRead.details.map(book => (
        <StyledCol key={book.title.split(' ').join('_')} xs="6" md="4" lg="2" className="mb-4 align-items-center d-flex">
          <img src={process.env.PUBLIC_URL + book.image} alt={`${book.title} by ${book.author}`} />
        </StyledCol>
      ))}
    </Row>
  )
};

export default Reading;
