import { useState } from "react";
import { Modal, Button } from "react-bootstrap";
import { ExperienceDetailsModalProps } from "./ExperienceDetailsModal.types";

import './ExperienceDetailsModal.scss';

const ExperienceDetailsModal:React.FC<ExperienceDetailsModalProps> = ({experience, onCloseCallback}) => {
  const [show, setShow] = useState(true);

  const handleClose = () => {

    if (onCloseCallback) {
      onCloseCallback(undefined);
    }
    setShow(false);
  }

  return (
    <Modal show={show} onHide={handleClose} size='xl' centered={true}>
      <Modal.Header closeButton>
        <Modal.Title>{experience.organization}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <h5 className="exp__title fs-5">
          {experience.title} <span className="text-muted fs-6">({`${experience.from.toFormat('LLL yyyy')} - ${experience.to?.toFormat('LLL yyyy') || 'Present'}`})</span>
        </h5>
        <h6 className="text-muted fs-6">{experience.city},{experience.country}</h6>
        <p className="fs-5 mt-3">Tasks</p>
        <ul className="list-unstyled">
          {experience.tasks && experience.tasks.map((task:string) => <li key={task.split(' ').join('_')} className="exp__task mt-2"><i className="bi bi-caret-right-fill"></i>  {task}</li>)}
        </ul>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          Close
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default ExperienceDetailsModal;
