import { DateTime } from "luxon";

export interface WorkExpSchema {
  organization: string;
  imgsrc: string;
  from: Date|string;
  summary: string;
  to?: Date|string;
  isCurrent: boolean;
  title: string;
  tasks: string[];
  city: string;
  country: string;
}


export class WorkExperience {

  constructor (expDetails: WorkExpSchema) {

    this.organization = expDetails.organization;
    this.imgsrc = expDetails.imgsrc;
    this.from = DateTime.fromJSDate(new Date(expDetails.from));
    this.summary = expDetails.summary;
    this.to = expDetails.to? DateTime.fromJSDate(new Date(expDetails.to)): null;
    this.isCurrent = expDetails.isCurrent;
    this.title = expDetails.title;
    this.tasks = expDetails.tasks;
    this.city = expDetails.city;
    this.country = expDetails.country;
  }

  organization: string;
  imgsrc: string;
  from: DateTime;
  summary: string;
  to?: DateTime|null;
  isCurrent: boolean;
  title: string;
  tasks: string[];
  city: string;
  country: string;
}
