import { useState } from 'react';
import { Container, Tab, Row } from "react-bootstrap";
import { useTypedSelector } from "../../hooks/useTypedSelector";
import { InterestDetails } from "../../models/user-details.model";
import Reading from "./Reading/Reading";
import { StyledTab, StyledInterestWrapper } from "./Interest.style";
import Photography from "./Photography/Photography";
import Drawing from "./Drawing/Drawing";

const Interest: React.FC = () => {

  const [activeTab, setActiveTab] = useState('reading');

  const { userDetails } = useTypedSelector(state => state.user);

  const handleSelection = (selection: string | null) => {
    if (selection) {
      setActiveTab(selection);
    }
  };

  const renderInterestDetails = (interest: InterestDetails) => {
    switch (interest.type) {
      case ('reading'): {
        return (
          <Tab
            key={interest.type}
            eventKey={interest.type}
            title="📖 Reading">
            <Reading booksRead={interest} />
          </Tab>
        )
      }
      case ('photography'): {
        return (
          <Tab
            key={interest.type}
            eventKey={interest.type}
            title="📷 Photography">
            <Photography photosTaken={interest} />
          </Tab>
        )
      }
      case ('drawing'): {
        return (
          <Tab
            key={interest.type}
            eventKey={interest.type}
            title="✏️ Drawing">
            <Drawing drawings={interest} />
          </Tab>
        )
      }
    }
  };

  return (
    <Container>
      <Row>
        <StyledInterestWrapper>
          <StyledTab
            id="interest-tab"
            className="my-4"
            onSelect={handleSelection}
            justify
            activeKey={activeTab}
          >
            {userDetails?.interestDetails && userDetails.interestDetails.map(interest => renderInterestDetails(interest))}
          </StyledTab>
        </StyledInterestWrapper>
      </Row>

    </Container>
  );
}

export default Interest;
