import { DateTime } from "luxon";
import { useRef } from "react";
import { Col, Container, Row } from "react-bootstrap";
import useIsElementInViewport from "../../hooks/useIsElementInViewport";
import { useTypedSelector } from "../../hooks/useTypedSelector";

import './PersonalDetails.scss';

const PersonalDetails:React.FC = () => {
  const {userDetails} = useTypedSelector(state => state.user);

  const titleRef = useRef() as React.MutableRefObject<HTMLHeadingElement>;
  const isInViewport = useIsElementInViewport(titleRef);

  const getExperience = () => {
    if (userDetails?.about.workingSince) {
      const timeNow = DateTime.now();
      const diffInYears = timeNow.diff(userDetails.about.workingSince, ['years']).years;

      return Math.floor(diffInYears);

    }
    return 0;
  }
  
  return (
    <>
      <Container fluid={true} className="sp-info d-flex align-items-center">
        <Container>
          <Row>
            <Col xs="12" lg="6" className="my-4 py-4">
              <h1 className={`sp-info__title animate-me ${isInViewport ? 'in-view': ''}`} ref={titleRef}>
                {userDetails && <span className="sp-info__title-lines">Hello I'm, {userDetails.firstName} {userDetails.lastName}.</span>}
                <span className="sp-info__title-lines">I'm a &lt;Web Developer&gt;.</span>
              </h1>
              <p className={`sp-info__desc mt-3 animate-me ${isInViewport ? 'in-view': ''}`}>
                <span>I am a software engineer and architect specializing in building fluid, responsive, high performant and accessible experiences. 
                I have over {getExperience()} years of experience in consulting industry, and currently I am focused 
                on building high performance web-apps at <span className="sp-info__desc-org">Deloitte</span>.</span>
              </p>
            </Col>
            <Col xs="12" lg="6" className="my-4 py-4">
              <Row>
                <Col xs="12" lg="12" className="sp-info__interests scroll-animation text-md-center text-lg-end text-sm-center fade-in">
                  <p className="fs-1">
                    I am interested in
                    <span className="sp-info__interests__scroller scroller">
                      <span className="scroll-container text-md-center text-lg-end text-sm-center">
                        {userDetails?.interests.map(interest => (<span className="scroller-item" key={interest.split(' ').join('_')}>{interest} <br /></span>))}
                      </span>
                    </span>
                  </p>
                </Col>
              </Row>
              <Row>
                <Col xs="12" lg="12" className="sp-info__askme scroll-animation text-end">

                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
      </Container>
    </>
  );
}

export default PersonalDetails;
