import { Popover, OverlayTrigger, Row, Col } from "react-bootstrap";
import { useParams } from 'react-router-dom';

import { TechDetails } from "../../../models/tech-details.model";
import Rating from "./Rating/Rating";
import './Tech.scss';

interface TechProps {
  tech: TechDetails,
  view: 'grid' | 'list'
}

const Tech: React.FC<TechProps> = ({ tech, view }) => {
  const progressPercentage = tech.proficiency / 5 * 100;
  const params = useParams();
  const selectedView =  params.view || view;

  const getPopover = () => {
    return (
      <Popover className="tech__popover">
        <Popover.Header as="h3">{tech.label}<span className="text-muted"> - {progressPercentage}%</span></Popover.Header>
        <Popover.Body>
          <ul>
            {tech.additionalInputs.map(item => (<li key={item.split(' ').join('_')}>{item}</li>))}
          </ul>
        </Popover.Body>
      </Popover>
    );
  }

  return (
    <div className={`tech ${selectedView === 'grid' ? 'col-lg-4 col-md-6 col-xs-12 grid' : 'col-md-6 col-xs-12 list'}`}>
      <OverlayTrigger rootClose trigger={['click', 'focus']} placement="auto" overlay={getPopover()}>
        <div className="tech__wrapper">
          {selectedView === 'grid' && <div className={`tech__pie pie-wrapper progress-${progressPercentage}`}>
            <span className={`tech__pie__label ${tech.label.length > 15 ? 'multiline': ''}`}>{tech.label} <span className="visually-hidden">Proficiency {progressPercentage}%.</span> </span>
            <div className="tech__pie__main">
              <div className="tech__pie__left-side half-circle"></div>
              <div className="tech__pie__right-side half-circle"></div>
            </div>
          </div>}
          {selectedView === 'list' && (
            <Row className="tech__list__wrapper my-2">
              <Col xs="6" lg="5">
                <div className={`tech__list progress-${progressPercentage}`}>
                  <span className={`tech__list__label`}>{tech.label} <span className="visually-hidden">Proficiency {progressPercentage}%.</span> </span>
                </div>
              </Col>
              <Col xs="6" lg="7">
                <Rating skillName={tech.label} rating={tech.proficiency} />
              </Col>
            </Row>
          )}
        </div>
      </OverlayTrigger>
    </div>
  );
}

export default Tech;
