import { useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { Container, Row, Col, Button } from "react-bootstrap";
import { useTypedSelector } from "../../hooks/useTypedSelector";
import Tech from './Tech/Tech';
import { StyledContainer } from "./TechDetails.styles";

import './TechDetails.styles.tsx';

const TechDetails: React.FC = () => {

  const [viewMode, setViewMode] = useState<'grid' | 'list'>('grid');

  const { loading, error, techDetails } = useTypedSelector(state => state.techDetails);

  const navigate = useNavigate();

  const { view } = useParams();

  const handleViewChange = (viewMode: 'grid' | 'list') => {
    navigate(`/tech/${viewMode}`);
    setViewMode(viewMode);
  }

  return (
    <StyledContainer fluid={true} className="tech-details-wrapper">
      <Container>
        <Row>
          <Col xs="12" lg="8" className="mt-4 mb-4 py-4 tech-details__title">
            <h3>List of Technologies I've worked with -</h3>
            <h4 className="text-muted">click on individual items for more details.</h4>
          </Col>
          <Col xs="12" lg="4" className="mt-4 mb-4 py-4 tech-details__view">
            <Button variant="secondary" className={`mx-3 ${view === 'grid' ? 'active' : ''}`} onClick={(e) => handleViewChange('grid')}><i className="bi bi-grid fs-3"></i><span className="visually-hidden">Grid Layout</span></Button>
            <Button variant="secondary" className={`${view === 'list' ? 'active' : ''}`} onClick={(e) => handleViewChange('list')}><i className="bi bi-list fs-3"></i><span className="visually-hidden">List Layout</span></Button>
          </Col>
          {loading && <Col>Loading...</Col>}
          <Row className="tech__details__main">
            {techDetails ? techDetails.map(detail => (
              <Tech tech={detail} key={detail.label} view={viewMode} />
            )) : null}
          </Row>
          {error && <Col>{error}</Col>}
        </Row>
      </Container>
    </StyledContainer>
  );
}

export default TechDetails;
