import { StyledRatingContainer } from './Rating.styles';

interface RatingProps {
  rating: number,
  skillName: string
}

const Rating: React.FC<RatingProps> = ({ rating, skillName }) => {
  const renderRatingItems = () => {
    const items = [];
    let tempRating = rating;

    for (let i = 0; i < 5; i++) {
      const className = tempRating >= 1 ? 'percent-100' : (tempRating > 0 ? `percent-${tempRating.toString().replace('0.', '').padEnd(2, '0')}` : 'percent-0');
      items.push(<span key={skillName + rating + i} className={`rating__item ${className}`} aria-hidden="true"></span>);
      tempRating -= 1;
    }

    return items;
  }

  return (
    <StyledRatingContainer className="rating" title={`${rating} out of 5`}>
      {renderRatingItems()}
    </StyledRatingContainer>
  );
}

export default Rating;
