import { Provider } from "react-redux";
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { store } from "../../state";

import Header from "../Header/Header";
import Interest from "../Interests/Interest";
import PersonalDetails from "../PersonalDetails/PersonalDetails";
import TechDetails from "../TechDetails/TechDetails";
import WorkExperienceView from "../WorkExperience";
import "./App.scss";

const App: React.FC = () => {
  return (
    <Provider store={store}>
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Header />}>
            <Route index element={<PersonalDetails />} />
            <Route path="about" element={<PersonalDetails />} />
            <Route path="experience" element={<WorkExperienceView />} />
            <Route path="tech" element={<TechDetails />} />
            <Route path="tech/:view" element={<TechDetails />} />
            <Route path="interests" element={<Interest />} />
          </Route>
        </Routes>
      </BrowserRouter>
    </Provider>
  );
};

export default App;
