import axios from 'axios';
import { Dispatch } from 'redux';
import { UserDetails, UserDetailsInput } from '../../models/user-details.model';
import { UserActionTypes } from '../action-types';
import { UserAction } from '../actions';

export const fetchUserDetails = () => {
  return async (dispatch: Dispatch<UserAction>) => {
    dispatch({
      type: UserActionTypes.FETCH_USER_DETAILS,
    });

    try {
      const { data } = await axios.get<UserDetailsInput>(process.env.PUBLIC_URL + '/mock-data/user-details.json');
      const userDetails =new UserDetails(data);

      dispatch({
        type: UserActionTypes.FETCH_USER_DETAILS_SUCCESS,
        payload: userDetails,
      });
    } catch (err:any) {
      dispatch({
        type: UserActionTypes.FETCH_USER_DETAILS_FAILURE,
        payload: err.message,
      });
    }
  };
};
