import { ExperienceTileProps } from "./ExperienceTile.types";
import './ExperienceTile.scss';
import { Button } from 'react-bootstrap';
import { useRef } from "react";
import useIsElementInViewport from "../../../hooks/useIsElementInViewport";

const ExperienceTile: React.FC<ExperienceTileProps> = ({experience, setSelectionCallback}) => {

  const tileRef = useRef() as React.MutableRefObject<HTMLDivElement>;

  const isInViewport = useIsElementInViewport(tileRef);

  return (
    <div className={`sp-xptile ${isInViewport ? 'in-view' : ''}`} ref={tileRef}>
      <div className="card">
      <h5 className="card-header">{experience.organization}</h5>
        {/* <img src={process.env.PUBLIC_URL + experience.imgsrc} className="card-img-top" alt={experience.organization} /> */}
        <div className="card-body">
          <h5 className="card-title">{experience.title}</h5>
          <h6 className="card-subtitle mb-2 mt-1 text-muted">{`${experience.from.toFormat('LLL yyyy')} - ${experience.to?.toFormat('LLL yyyy') || 'Present'}`}</h6>
          <p className="card-text mt-4">{experience.summary}</p>
          <Button variant='primary' className="mt-3" onClick={evt => setSelectionCallback(experience)}>Additional Details for Role</Button>
        </div>
      </div>
    </div>
  )
};

export default ExperienceTile;
