import { WorkExperience } from '../../models/work-experience.model';
import { WorkExperienceActionType } from '../action-types';
import { WorkExpAction } from '../actions';

interface RepositoriesState {
  loading: boolean;
  error: string | null;
  data: WorkExperience[];
}

const initialState = {
  loading: false,
  error: null,
  data: [],
};

const reducer = (
  state: RepositoriesState = initialState,
  action: WorkExpAction
): RepositoriesState => {
  switch (action.type) {
    case WorkExperienceActionType.FETCH_WORK_EXP:
      return { loading: true, error: null, data: [] };
    case WorkExperienceActionType.FETCH_WORK_EXP_SUCCESS:
      return { loading: false, error: null, data: action.payload };
    case WorkExperienceActionType.FETCH_WORK_EXP_FAILURE:
      return { loading: false, error: action.payload, data: [] };
    default:
      return state;
  }
};

export default reducer;
